
















































import Vue from "vue";
import { customMapState } from "@/helpers/mapSatate";
import { IRootState } from "@/store/models/store";
import { mapActions } from "vuex";

interface Items {
  key: string;
  description: string;
  range: [number, number];
}

export default Vue.extend({
  data() {
    return {
      fields: [
        { key: "key", label: "Позначення" },
        { key: "description", label: "Опис" },
        { key: "range", label: "Проміжок" }
      ]
    };
  },

  computed: {
    ...customMapState({
      termSet: (s: IRootState) => s.settings.termSet
    }),

    items(): Items[] {
      return Object.entries(this.termSet).map(
        ([key, { description, range }]) => ({
          key,
          description,
          range
        })
      );
    }
  },

  methods: {
    ...mapActions({
      setValueToTermSetRange: "settings/setValueToTermSetRange"
    })
  }
});
